import * as R from "ramda";

export let wordFrequencyMap = {}; // Stocke les mots avec leurs fréquences

/**
 * Initialise la fréquence des mots du corpus à partir des tokens
 * @param {string[]} tokens - Liste de tokens extraits du corpus
 */
export function setCorpusWords(tokens) {
    wordFrequencyMap = {};
    tokens.forEach(token => {
        const word = token.toLowerCase();
        wordFrequencyMap[word] = (wordFrequencyMap[word] || 0) + 1;
    });
}

/**
 * Prédit les probabilités des prochaines lettres en fonction du corpus
 * @param {string} prefix - Début du mot en cours d'écriture
 * @returns {Object} - Objet { lettre: probabilité } trié par probabilité décroissante
 */
export function predictNextLetterProbs(prefix) {
    const prefixLower = prefix.toLowerCase();
    const counts = {};
    let total = 0;

    Object.entries(wordFrequencyMap).forEach(([word, freq]) => {
        if (word.startsWith(prefixLower) && word.length > prefixLower.length) {
            const nextChar = word[prefixLower.length];
            counts[nextChar] = (counts[nextChar] || 0) + freq;
            total += freq;
        }
    });

    if (total === 0) return { " ": 1 };

    return R.pipe(
        R.mapObjIndexed(count => count / total),
        R.toPairs,
        R.sortBy(R.prop(1)),
        R.reverse,
        R.fromPairs
    )(counts);
}