import * as R from 'ramda';

import {
    predictNextLetterProbs,
    setCorpusWords
} from "../predictionRamda.mjs";



const Keyboard = {
    elements: {
        main: null,
        keysContainer: null,
        keys: [],
        capsKey: null,
    },

    properties: {
        value: "",
        capsLock: false,
        keyboardInputs: null,
        keyLayout: [
            "1", "2", "3", "4", "5", "6", "7", "8", "9", "0", "backspace",
            "a", "z", "e", "r", "t", "y", "u", "i", "o", "p",
            "caps", "q", "s", "d", "f", "g", "h", "j", "k", "l", "m", "enter",
            "done", "w", "x", "c", "v", "b", "n", ",", ";", ":", "!", "space",
        ],
    },

    init() {
        // create and setup main element
        this.elements.main =
            document.createElement("div");
        this.elements.main.classList
            .add("keyboard");
        document.body
            .appendChild(this.elements.main);

        // create and setup child container component
        this.elements.keysContainer =
            document.createElement("div");
        this.elements.keysContainer
            .classList.add("keyboard__keys");
        this.elements.main
            .appendChild(this.elements.keysContainer);

        // create and setup key elements
        this.elements.keysContainer
            .appendChild(this._createKeys());
        this.elements.keys =
            this.elements.keysContainer
                .querySelectorAll(".keyboard__key");

        // open keyboard for elements with .use-keyboard-input
        this.properties.keyboardInputs =
            document.querySelectorAll(
                ".use-keyboard-input"
            );
        this.properties
            .keyboardInputs
            .forEach((element) => {
                element.addEventListener("focus", () => {
                    this
                        .open(element.value, (currentValue) => {
                            element.value = currentValue;
                        });
                });
            });
    },

    _createIconHTML(icon_name) {
        return `<span class="material-icons">${icon_name}</span>`;
    },

    _createKeyBtn(iconName = "", class1 = "", onclick = () => {}, class2 = "", dataChar = null) {
        this.keyElement = document.createElement("button");

        this.keyElement.setAttribute("type", "button");
        this.keyElement.classList.add("keyboard__key");

        if (class1) this.keyElement.classList.add(class1);
        if (class2) this.keyElement.classList.add(class2);

        if (iconName) {
            this.keyElement.innerHTML = this._createIconHTML(iconName);
        }

        if (dataChar !== null) {
            this.keyElement.dataset.char = dataChar;
        }

        this.keyElement.addEventListener("click", onclick);

        return this.keyElement;
    },


    _createKeys() {
        const fragment =
            document.createDocumentFragment();

        this.properties.keyLayout.forEach((key) => {
            const insertLineBreak =
                ["backspace", "p", "enter", "?"].indexOf(key) !== -1;

            switch (key) {
                case "backspace":
                    this._createKeyBtn(
                        "backspace", "keyboard__key--wide",
                        () => {
                            this.properties.value =
                                this.properties.value.slice(0, -1);
                            this._updateValueInTarget();
                        });
                    break;

                case "caps":
                    this._createKeyBtn(
                        "keyboard_capslock",
                        "keyboard__key--activatable",
                        () => {
                            this.elements.capsKey
                                .classList
                                .toggle("keyboard__key--active");
                            this._toggleCapsLock();
                        },
                        "keyboard__key--wide"
                    );
                    this.elements.capsKey = this.keyElement;
                    break;

                case "enter":
                    this._createKeyBtn(
                        "keyboard_return", "keyboard__key--wide",
                        () => {
                            this.properties.value += "\n";
                            this._updateValueInTarget();
                        });
                    break;

                case "space":
                    const spaceBtn = this._createKeyBtn(
                        "space_bar",
                        "keyboard__key--extra--wide",
                        () => {
                            this.properties.value += " ";
                            this._updateValueInTarget();
                        },
                        "", // pas de class2
                        " " // data-char pour la touche espace
                    );
                    fragment.appendChild(spaceBtn);
                    break;


                case "done":
                    this._createKeyBtn(
                        "check_circle",
                        "keyboard__key--dark",
                        () => {
                            this.close();
                            this._updateValueInTarget();
                        },
                        "keyboard__key--wide"
                    );
                    break;

                // Partie du clavier avec les corrections pour la prédiction des lettres

// Dans la fonction _createKeys(), cas "default" corrigé :
                default:
                    this._createKeyBtn("", "", () => {
                        /// Ajout du caractère
                        this.properties.value += this.properties.capsLock
                            ? key.toUpperCase()
                            : key.toLowerCase();

                        this._updateValueInTarget();

// Analyse du texte actuel
                        const currentValue = this.properties.value.trim().split(/\s+/);
                        const lastWord = currentValue[currentValue.length - 1] || " ";

// On n'utilise plus le contexte, la prédiction est purement basée sur le corpus
                        if (!corpusReady) {
                            console.warn("Le corpus n'est pas encore prêt !");
                            return;
                        }

                        const letterProbs = predictNextLetterProbs(lastWord);

                        console.log("Valeur tapée :", this.properties.value);
                        console.log("Mot en cours :", lastWord);
                        console.log("Letter probs:", letterProbs);

// Appliquer les prédictions de lettres sur les touches
                        this.elements.keys.forEach((keyEl) => {
                            keyEl.classList.remove("activeYellow", "activePurple");

                            let char;
                            if (keyEl.dataset.char) {
                                char = keyEl.dataset.char.toLowerCase();
                            } else if (keyEl.textContent && keyEl.textContent.trim()) {
                                char = keyEl.textContent.trim().toLowerCase();
                            } else {
                                return; // Ignorer les touches non pertinentes
                            }

                            if (char === ' ' || char.length !== 1 || /[^a-z0-9]/.test(char)) return;

                            const prob = letterProbs[char] || 0;

                            if (prob > 0.15) {
                                keyEl.classList.add("activePurple");
                            } else if (prob > 0.05) {
                                keyEl.classList.add("activeYellow");
                            }
                        });
                    });


                        // Affichage de la lettre sur la touche et ajout du data-char
                    this.keyElement.textContent = key.toLowerCase();
                    this.keyElement.dataset.char = key.toLowerCase(); // Ajout pour cohérence
                    break;
            }

            fragment.appendChild(this.keyElement);

            if (insertLineBreak) {
                fragment
                    .appendChild(document.createElement("br"));
            }
        });
        return fragment;


    },

    _updateValueInTarget() {
        this.properties
            .keyboardInputs
            .forEach((keyboard) => {
                keyboard.value =
                    this.properties.value;
            });
    },

    _toggleCapsLock() {
        this.properties.capsLock =
            !this.properties.capsLock;

        for (let key of this.elements.keys) {
            if (key.childElementCount === 0) {
                key.textContent =
                    this.properties.capsLock
                        ? key.textContent.toUpperCase()
                        : key.textContent.toLowerCase();
            }
        }
    },

    open(initialValue, oninput) {
        this.properties.value =
            initialValue || "";
        this.elements.main
            .classList
            .remove("keyboard--hidden");
        this.elements.main.classList.add("keyboard--visible");
    },

    close() {
        this.elements.main.classList.remove("keyboard--visible");
        this.properties.value = this.properties.value;
        this.elements.main
            .classList.add("keyboard--hidden");
    },


};

let tokens = []; // Variable globale pour stocker les tokens

function tokenizeText(text) {
    return text
        .toLowerCase()
        .replace(/[^a-z0-9\s!:;,]/gi, ' ')
        .split(/\s+/)
        .filter(Boolean);
}

let corpusReady = false;

// Corpus de secours si le chargement échoue
const FALLBACK_CORPUS = `
    le chat dort paisiblement sur le canapé pendant que le chien court dans le jardin
    la pluie tombe doucement sur les toits de la ville endormie
    un bon livre et une tasse de thé chaud font le bonheur des jours de pluie
    les oiseaux chantent à l'aurore pour saluer le lever du soleil
    la connaissance s'acquiert par l'expérience et l'observation
`;

// Fonction pour vérifier la disponibilité de l'API Electron
async function checkElectronAPI() {
    if (!window.electronAPI) {
        console.warn("L'API Electron n'est pas disponible dans le navigateur");
        return false;
    }
    
    try {
        const pingResponse = window.electronAPI.ping ? await window.electronAPI.ping() : null;
        if (pingResponse === 'pong') {
            console.log("✅ L'API Electron est disponible et fonctionnelle");
            return true;
        } else {
            console.warn("L'API Electron ne répond pas comme prévu");
            return false;
        }
    } catch (error) {
        console.error("Erreur lors de la vérification de l'API Electron:", error);
        return false;
    }
}

async function loadCorpus() {
    console.log("Début du chargement du corpus...");
    
    try {
        const isElectronAvailable = await checkElectronAPI();
        let corpusText = '';
        
        if (isElectronAvailable) {
            console.log("Tentative de chargement du corpus via l'API Electron...");
            try {
                corpusText = await window.electronAPI.loadCorpus();
                console.log("Réponse de l'API reçue, longueur:", corpusText?.length || 0);
                
                if (!corpusText || corpusText.trim() === '') {
                    throw new Error("Le corpus est vide");
                }
                console.log("Corpus chargé avec succès via l'API Electron");
            } catch (apiError) {
                console.error("Erreur lors du chargement via l'API:", apiError);
                throw apiError; // On laisse le catch externe gérer la suite
            }
        } else {
            console.warn("API Electron non disponible, utilisation du corpus de secours");
            corpusText = FALLBACK_CORPUS;
        }
        
        console.log("Traitement du texte du corpus...");
        tokens = tokenizeText(corpusText);
        
        if (tokens.length === 0) {
            throw new Error("Aucun token valide trouvé dans le corpus");
        }
        
        console.log("Définition des mots du corpus...");
        setCorpusWords(tokens);
        corpusReady = true;
        console.log("✅ Corpus prêt - ", tokens.length, "tokens chargés");
        
    } catch (error) {
        console.error("❌ Erreur critique lors du chargement du corpus:", error);
        console.warn("Utilisation du corpus de secours...");
        
        // Utiliser le corpus de secours en cas d'erreur
        tokens = tokenizeText(FALLBACK_CORPUS);
        setCorpusWords(tokens);
        corpusReady = true;
        console.log("✅ Corpus de secours chargé avec succès -", tokens.length, "tokens");
    }
}

// Démarrer le chargement du corpus immédiatement
loadCorpus().catch(console.error);

window.addEventListener("DOMContentLoaded", () => {
    Keyboard.init();
    // Ajout d'une animation douce pour le clavier
    setTimeout(() => {
        Keyboard.elements.main.classList.add("keyboard--visible");
    }, 100); // petit délai pour déclencher la transition CSS

});



